@import './helpers/index';
@import './right-panel';

body {
  display: flex;
  flex-direction: column;
  margin: 0;
  height: calc(100% - 4px - 85px);
  padding-top: 85px;
  //padding-bottom: 70px;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

a {
  text-decoration: none;

  &:visited {
    color: inherit;
    overflow: visible
  }
}

hr.gradient {
  border-image-source: linear-gradient(to right, transparent -5%, currentColor, transparent 105%);
  border-image-slice: 1;
  border-image-width: 1;
}

.border-gradient {
  border-image: linear-gradient(to right, transparent -20%, currentcolor, transparent 120%);
  border-image-slice: 1;
}
